.hero {width:100%;
        height:100vh;
        background-image:url('/portfolio/wp-content/themes/wordpress_starter_theme/images/room.jpg');
        background-size:cover;
        background-repeat:no-repeat;
        position:relative;
        .transporter{
            color:$colorWhite;
            position:absolute;
            font-family:$font-contrail;
            font-size:3rem;
            top:50%;
            left:50%;
            transform:translate(-50%,-50%); 
            font-style: italic;
            opacity:1;
            font-weight: 700;
            transition: color 0.5s;
             // background: radial-gradient(ellipse at center, rgba(189, 91, 87, 0) 40%, rgba(189, 91, 87, 0.3) 45%, rgba(189, 91, 87, 0) 50%, rgba(189, 91, 87, 0) 100%);
        border-radius: 50%;
        // border:1px solid red;
        width:100vh;
        height:100vh;
        line-height:100vh;
        text-align:center;
        opacity:0;
        animation: circlensflare 1s, textGlow 3s forwards;
        animation-delay: .5s;
      
        }
        }


.transporter span.subtitle {
            display:inline-block;
            // transform:rotate(-20deg);
            transform-origin: left center;
            transform: rotateY(90deg);
            color:$colorBlue;
            animation: flip 1s forwards;
            animation-delay:1.6s;}

@keyframes flip {

0{transform: rotateY(90deg);

}


100%{transform: rotateY(0deg);

}

}

