@keyframes textGlow {
0%{ opacity:0;
    text-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 20px rgba(255, 255, 255, 1), 0 0 30px rgba(255, 255, 255, 1), 0 0 40px $colorBlue, 0 0 70px $colorBlue, 0 0 80px $colorBlue, 0 0 100px $colorBlue;
}
1% {
    opacity:1;
    text-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 20px rgba(255, 255, 255, 1), 0 0 30px rgba(255, 255, 255, 1), 0 0 40px $colorBlue, 0 0 70px $colorBlue, 0 0 80px $colorBlue, 0 0 100px $colorBlue;

}

100%{
    text-shadow:none;
    opacity:1;
}

}

